.navbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 80px;
    margin: auto;
    font-family: 'Marcellus', serif, sans-serif;
    z-index: 1;
    text-shadow: 0 0 0.3em #E100FF, 0 0 0.3em #E100FF, 0 0 #E100FF;
  }


  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    grid-row-gap: 0px;
    row-gap: 0px;
    list-style: none;
    text-align: center;
    width: 100%;
    justify-content: center;
    font-size: 20px;
    padding: 0px;
  }

  .nav-item {
    display: flex;
    align-items: center;
    height: 60px;
  }


  .nav-links {
      color: white;
      text-decoration: none;
    }

  .nav-links:hover {
    color: #E100FF;
    text-shadow: 0 0 0.3em #E100FF, 0 0 0.3em #E100FF, 0 0 #E100FF;
  }

  .menu-icon {
    display: none;
  }


  @media screen and (max-width: 800px) {

    .navbar {
      position: fixed;
      height: 70px;
    }

    .menu-icon {
      display: block;
      position: absolute;
      top: 5px;
      right: 15px;
      -webkit-transform: translate(-90%, 25%);
              transform: translate(-90%, 25%);
      font-size: 2rem;
      cursor: pointer;
      color: white;
    }

    .fa-times {
      color: #fff;
      font-size: 2.5rem;
    }

    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 70vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }

    .nav-menu.active {
      background: #2f0036;
      position: absolute;
      top: 80px;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 6;
      width: 100%;
      height: 85vh;
      padding: 30px 0px;
    }

    .nav-item {
      height: 80px;
    }

    .nav-links {
      text-align: center;
      padding: 1rem;
      width: 100%;
      display: table;
    }

    .nav-links:hover {
      color: #E100FF;
    }
}
